/**
 * Palettized image format
 * 4 bits per pixel (16 colors), stored as 2 pixels per byte
 * High nibble = left pixel, low nibble = right pixel
 */
export interface PalettizedImage {
    width: number;
    height: number;
    data: Uint8Array; // length = ceil(width * height / 2)
    palette?: Uint32Array; // optional RGBA palette (16 colors)
}

/**
 * Create an empty palettized image
 */
export function createPalettizedImage(
    width: number,
    height: number,
    palette?: Uint32Array,
): PalettizedImage {
    const size = Math.ceil((width * height) / 2);
    return {
        width,
        height,
        data: new Uint8Array(size),
        palette,
    };
}

/**
 * Get pixel value at (x, y)
 * Returns index 0-15
 */
export function getPixelPal(
    img: PalettizedImage,
    x: number,
    y: number,
): number {
    const pixelIndex = y * img.width + x;
    const byteIndex = Math.floor(pixelIndex / 2);
    const isHighNibble = pixelIndex % 2 === 0;

    if (isHighNibble) {
        return (img.data[byteIndex] >> 4) & 0x0f;
    } else {
        return img.data[byteIndex] & 0x0f;
    }
}

/**
 * Set pixel value at (x, y)
 * value must be 0-15
 */
export function setPixelPal(
    img: PalettizedImage,
    x: number,
    y: number,
    value: number,
): void {
    const pixelIndex = y * img.width + x;
    const byteIndex = Math.floor(pixelIndex / 2);
    const isHighNibble = pixelIndex % 2 === 0;

    value = value & 0x0f; // ensure 0-15

    if (isHighNibble) {
        img.data[byteIndex] = (img.data[byteIndex] & 0x0f) | (value << 4);
    } else {
        img.data[byteIndex] = (img.data[byteIndex] & 0xf0) | value;
    }
}

/**
 * Convert palettized image back to RGBA for export
 */
export function palettizedToRGBA(img: PalettizedImage): { width: number; height: number; data: Uint8ClampedArray } {
    const palette = img.palette || DEFAULT_PALETTE;
    const rgbaData = new Uint8ClampedArray(img.width * img.height * 4);
    
    for (let y = 0; y < img.height; y++) {
        for (let x = 0; x < img.width; x++) {
            const paletteIdx = getPixelPal(img, x, y);
            const color = palette[paletteIdx];
            
            const pixelOffset = (y * img.width + x) * 4;
            rgbaData[pixelOffset] = color & 0xFF;           // R
            rgbaData[pixelOffset + 1] = (color >> 8) & 0xFF;  // G
            rgbaData[pixelOffset + 2] = (color >> 16) & 0xFF; // B
            rgbaData[pixelOffset + 3] = (color >> 24) & 0xFF; // A
        }
    }
    
    return {
        width: img.width,
        height: img.height,
        data: rgbaData,
    };
}

/**
 * Default palette
 * The classic 8-bit color palette, https://en.wikipedia.org/wiki/List_of_monochrome_and_RGB_color_formats#3-bit_RGB,
 * with orange instead of yellow to improve visibility, plus one gray.
 */
export const DEFAULT_PALETTE: Uint32Array = new Uint32Array([
    0xffffffff, // 0: white
    0x000000ff, // 1: black
    0xff0000ff, // 2: red
    0x00ff00ff, // 3: green
    0x0000ffff, // 4: blue
    0xffaa00ff, // 5: orange (yellow is too similar to white)
    0xff00ffff, // 6: magenta
    0x00ffffff, // 7: cyan
    0x808080ff, // 8: gray
]);
